\n\nhtml, body, div, span, applet, object, iframe,\nh1, h2, h3, h4, h5, h6, p, blockquote, pre,\na, abbr, acronym, address, big, cite, code,\ndel, dfn, em, img, ins, kbd, q, s, samp,\nsmall, strike, strong, sub, sup, tt, var,\nb, u, i, center,\ndl, dt, dd, ol, ul, li,\nfieldset, form, label, legend,\ntable, caption, tbody, tfoot, thead, tr, th, td,\narticle, aside, canvas, details, embed, \nfigure, figcaption, footer, header, hgroup, \nmenu, nav, output, ruby, section, summary,\ntime, mark, audio, video {\n\tmargin: 0;\n\tpadding: 0;\n\tborder: 0;\n\tfont-size: 100%;\n\tfont: inherit;\n\tvertical-align: baseline;\n}\n/* HTML5 display-role reset for older browsers */\narticle, aside, details, figcaption, figure, \nfooter, header, hgroup, menu, nav, section {\n\tdisplay: block;\n}\nbody {\n\tline-height: 1;\n}\nol, ul {\n\tlist-style: none;\n}\nblockquote, q {\n\tquotes: none;\n}\nblockquote:before, blockquote:after,\nq:before, q:after {\n\tcontent: '';\n\tcontent: none;\n}\ntable {\n\tborder-collapse: collapse;\n\tborder-spacing: 0;\n}\n","@use 'reset';\n@use 'layout';\n@use 'utilities';\n@use 'components';\n@use 'typography';\n@use 'eas';\n\nhtml {\n    font-size: 62.5%;\n}","// Flexbox Layouts\n.flex {\n    display: flex;\n\n    &-fd--c {\n        flex-direction: column;\n    }\n\n    // FD-C Mobile\n    &-fd--c-mobile {\n        flex-direction: column;\n\n        @media(min-width: 767px) {\n            flex-direction: row;\n        }\n    }\n\n    &-w-w {\n        flex-wrap: wrap;\n    }\n}\n\n// Grid Layouts\n.grid {\n    display: grid;\n\n    // 2 Col\n    &-gc--2 {\n        grid-template-columns: 1fr 1fr;\n\n        @media(min-width: 1024px) {\n            grid-template-columns: auto auto;\n        }\n\n        &-mobile {\n            grid-template-columns: 1fr;\n\n            @media(min-width: 767px) {\n                grid-template-columns: 1fr 1fr;\n            }\n        }\n    }\n\n    // 3 Col\n    &-gc--3 {\n        grid-template-columns: repeat(3, 1fr);\n    }\n\n    // 4 Col\n    &-gc--4 {\n        grid-template-columns: repeat(4, 1fr);\n    }\n\n    // Full\n    &-col--full {\n        grid-column: 1 / -1;\n\n      &-mobile {\n        @media(min-width: 1024px) {\n            grid-column: auto;\n        }\n      }\n    }\n\n    // Spans\n    &-s--2 {\n        grid-column: 1 / 3;\n    }\n\n    &-s--3 {\n        grid-column: 1 / 4;\n    }\n}\n\n// Flex\n.f {\n    &-1 {\n        flex: 1;\n    }\n}\n\n// Justify Content\n.jc {\n    &-c {\n        justify-content: center;\n    }\n\n    &-sb {\n        justify-content: space-between;\n    }\n}\n\n// Align Items\n.ai {\n    &-c {\n        align-items: center;\n    }\n}\n\n// Place Self\n.ps {\n    &-c {\n        place-self: center;\n    }\n\n    &-c-e {\n        place-self: center end;\n\n        @media(min-width: 767px) {\n            place-self: center;\n        }\n    }\n}\n\n// Display\n.disp {\n    &-block { display: block }\n}\n","// Variables\n:root {\n    --js-yellow: #facc15;\n    --white-gradient: radial-gradient(#eee, #fff);\n    --black: #030712;\n    --dark: #333;\n    --gray-light: #9ca3af;\n    --gray: #6b7280;\n    --gray-dark: #111827;\n    --red-light: #f87171;\n    --red: #b91c1c;\n    --red-dark: #7f1d1d;\n    --green-light: #a3e635;\n    --green: #4d7c0f;\n    --green-dark: #3f6212;\n    --blue-light: #60a5fa;\n    --blue: #1d4ed8;\n    --blue-dark: #1e40af;\n    --blue-gradient: linear-gradient(315deg, var(--blue) 0%, var(--blue-light) 100%);\n    --transition: all 0.25s;\n}\n\n// Colors\n.p {\n    &-win {\n        color: var(--green);\n        font-weight: 600;\n    }\n\n    &-lose {\n        color: var(--red);\n        font-weight: 600;\n    }\n}\n\n// Container\n.container {\n    max-width: 1200px;\n\n    &-rps {\n        height: 90%;\n        width: 90%;\n        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;\n    }\n\n    &-eas,\n    &-tic-tac-toe {\n        height: 512px;\n        width: 512px;\n        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;\n    }\n\n    &-calc {\n        background: #333;\n    }\n}\n\n.img {\n    width: 400px;\n}\n\n// Gap\n.gap {\n    &-1 { gap: 8px }\n    &-2 { gap: 16px }\n    &-3 { gap: 24px }\n}\n\n// Padding\n.py {\n    &-0 { padding-block: 8px }\n    &-1 { padding-block: 24px }\n    &-2 { padding-block: 32px }\n    &-3 { padding-block: 44px }\n    &-4 { padding-block: 80px }\n}\n\n.pi {\n    &-1 { padding-inline: 8px }\n    &-2 { padding-inline: 12px }\n}\n\n.pb {\n    &-1 { padding-bottom: 4px }\n}\n\n.p {\n    &-0 { padding: 4px }\n    &-1 { padding: 8px }\n    &-2 { padding: 12px }\n    &-3 { padding: 24px }\n}\n\n// Margin\n.my {\n    &-1 { margin-block: 12px }\n    &-2 { margin-block: 24px }\n    &-3 { margin-block: 36px }\n    &-4 { margin-block: 80px }\n}\n\n.mb {\n    &-1 { margin-bottom: 12px }\n    &-2 { margin-bottom: 24px }\n    &-3 { margin-bottom: 36px }\n    &-4 { margin-bottom: 64px }\n}\n\n.ml {\n    &-0 { margin-left: 4px }\n    &-1 { margin-left: 8px }\n}\n\n.mt {\n    &-1 { margin-top: 12px }\n    &-2 { margin-top: 24px }\n    &-3 { margin-top: 36px }\n}\n\n.m {\n    &-auto { margin-inline: auto }\n}\n\n// Text Align\n.ta {\n    &-c { text-align: center }\n    &-r { text-align: right }\n}\n\n// Lines\n.top-line {\n    border-top: 1px solid #ccc;\n}\n\n.bottom-line {\n    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;\n}\n\n// Hidden\n.hidden {\n    display: none;\n\n    &-mobile {\n        @media(min-width: 1024px) {\n            display: flex;\n        }\n    }\n}\n\n// Screen Size\n.full-screen {\n    max-height: 100vh;\n}\n\n.calc-display {\n    background: #fff;\n}","// Buttons\n.btn {\n    // General\n    cursor: pointer;\n    display: inline-block;\n    border: 2px solid transparent;\n    border-radius:10px;\n    color: var(--gray-9);\n    background: transparent;\n    font-size: 2rem;\n    transition: var(--transition);\n\n    &:active {\n        transform: scale(0.95);\n    }\n\n    &-menu {\n        @media(min-width: 1024px) {\n            display: none;\n        }\n    }\n\n    &-game {\n        background: var(--dark);\n        color: #fff;\n        \n        &:hover {\n            background: #555;\n        }\n    }\n\n    // Rock, Paper, Scissors\n    &-rps {\n        height: 150px;\n        width: 150px;\n        border-radius: 50%;\n        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;\n\n        &:hover {\n            transform: translateY(-2px);\n        }\n\n        &-rock {\n            color: var(--red);\n\n            &:hover {\n                background: var(--red-light);\n            }\n\n            &:active {\n                background: var(--red-dark);\n            }\n        }\n\n        &-paper {\n            color: var(--green);\n\n            &:hover {\n                background: var(--green-light);\n            }\n\n            &:active {\n                background: var(--green-dark);\n            }\n        }\n\n        &-scissors {\n            color: var(--blue);\n\n            &:hover {\n                background: var(--blue-light);\n            }\n\n            &:active {\n                background: var(--blue-dark);\n            }\n        }\n\n        &-choice {\n            font-size: 3.2rem;\n        }\n\n        &-choice-icon {\n            font-size: 4.4rem;\n        }\n    }\n\n    // Etch-a-Sketch\n    &-eas {\n        border: 1px solid var(--dark);\n\n        &.active {\n            background: var(--dark);\n            color: #fff;\n        }\n    }\n\n    // Calculator\n    &-calc {\n        background: #aaa;\n        color: #fff;\n\n        &:hover {\n            filter: brightness(0.95);\n        }\n    }\n\n    &-calc-alt {\n        background: #eee;\n        color: #333;\n    }\n\n    &-op {\n        background: #f59f00;\n    }\n\n    // Tic Tac Toe\n    &-cell {\n        background: #fff;\n        border: 2px solid #000;\n\n        &:hover {\n            background: #eee;\n        }\n    }\n}","// Fonts\n@import url('https://fonts.googleapis.com/css2?family=Nerko+One&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap');\n\nbody {\n    color: var(--black);\n    font-size: 1.6rem;\n    font-family: 'Open Sans', sans-serif;\n    line-height: 1.5;\n}\n\na {\n    text-decoration: none;\n    color: inherit;\n}\n\n// Anchor Tags\n.a {\n    display: inline-block;\n    transition: var(--transition);\n\n    &-main {\n        color: #333;\n    }\n\n    &-logo,\n    &-nav-link {\n        text-transform: uppercase;\n        font-family: 'Nerko One', cursive;\n    }\n\n    &-logo {\n        background: #fff;\n        border-radius: 20px;\n        color: var(--gray);\n        font-size: 3.4rem;\n\n        &:hover {\n            color: var(--gray-light);\n        }\n    }\n\n    &-nav-link {\n        font-size: 1.6rem;\n        color: #fff;\n        border-radius: 25px;\n        box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;\n        text-shadow: -1px 1px 0 #000,\n        1px 1px 0 #000,\n        1px -1px 0 #000,\n        -1px -1px 0 #000;\n                \n        &:hover {\n            text-shadow: -1px 1px 0 #fff,\n            1px 1px 0 #fff,\n            1px -1px 0 #fff,\n            -1px -1px 0 #fff;\n            transform: translateY(-2px);\n        }\n        \n        &:active {\n            transform: scale(0.95);\n        }\n\n        &:hover,\n        &:active {\n            box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;\n        }\n\n\n        &-rps {\n            background: url('../images/red-bg-animated.svg') no-repeat center center/cover;\n\n            &:hover {\n                color: var(--red-dark);\n            }\n        }\n\n        &-eas {\n            background: url('../images/green-bg-animated.svg') no-repeat center center/cover;\n\n            &:hover {\n                color: var(--green-dark);\n            }\n        }\n\n        &-calc {\n            background: url('../images/blue-bg-animated.svg') no-repeat center center/cover;\n\n            &:hover {\n                color: var(--blue-dark);\n            }\n        }\n    }\n\n    &-nav-link-ext {\n        border-color: var(--black);\n        background: url('../images/gray-bg-animated.svg') no-repeat center center/cover;\n\n        &:hover {\n            color: var(--black);\n        }\n    }\n}\n\n// Headings\n.h {\n    font-family: 'Nerko One', cursive;\n\n    &-main {\n        font-size: 4rem;\n    }\n\n    &-game {\n        font-size: 3rem;\n        color: var(--dark);\n    }\n}\n\n// Paragraphs\n.p {\n    &-main {\n        font-size: 1.8rem;\n    }\n\n    &-choice {\n        font-size: 3.6rem;\n        font-family: 'Nerko One', cursive;\n    }\n\n    &-footer {\n        font-size: 1.4rem;\n    }\n\n    &-game {\n        font-size: 2rem;\n        color: var(--dark);\n    }\n\n    &-calc,\n    &-calc-display {\n        font-size: 2rem;\n    }\n\n    &-calc-display {\n        font-style: italic;\n        color: #444;\n    }\n}\n\n// Icons\n.i {\n    &-game-player {\n        font-size: 4rem;\n\n        &-choice {\n            font-size: 3rem;\n        }\n    }\n}\n\n// Images\n.img {\n    &-preview {\n        display: none;\n\n        @media(min-width: 767px) {\n            display: block;\n        }\n    }\n}","// Etch a Sketch\n.eas {\n    // Grid\n    &-grid {\n        display: grid;\n        grid-template-columns: repeat(4, 1fr);\n        grid-template-rows: repeat(4, 1fr);\n    }\n    &-square {\n        cursor: pointer;\n        border: 1px solid var(--gray-light);\n        transition: var(--transition);\n\n        &:hover {\n            filter: brightness(0.75);\n        }\n    }\n}\n\n#eas-color {\n    appearance: none;\n    background-color: transparent;\n    width: 50px;\n    height: 50px;\n    border: none;\n    cursor: pointer;\n}\n\ninput[type=\"range\"]::-webkit-slider-runnable-track {\n    background-color: #ddd;\n}"],"sourceRoot":""}]);
